.containerLogin{
background: url("/Beira-Port-Cornelder.png");
opacity: 1;
top: 0;
left: 0;
bottom: 0;
right: 0;
position: absolute;
z-index: -1;   
background-repeat: no-repeat;
background-position:center;
background-size:cover;
}
.containerLogin{
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
}

@media (max-width: 1024px) {
	.containerLogin{
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

  .Cornelder-logo{
    width: 90%;
  }
	
